.main-dashboard-component {
  margin-bottom: 60px;

  h1 {
    color: var(--Text, #211b3a);
    font-family: Urbanist;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    /* 116.667% */
  }
}

.cards-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;

  .stats-card {
    border-radius: 10px;
    background: #fff;
    padding: 16px;
    height: 110px;
    transition: all 0.3s ease-in-out;
    .card-heading {
      display: flex;
      align-items: center;
      gap: 8px;

      h3 {
        color: var(--Text, #211b3a);
        font-family: Urbanist;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: 0.15px;
        margin: 0px;
      }
    }
  }
      .stats-card:hover{
      box-shadow:8px 21px 23px -33px black;
      cursor: pointer;
    }
}
.form-select option {
  font-size: 14px;   /* smaller text */
  padding: 4px 8px;  /* smaller padding */
      font-family: Urbanist;
      font-weight: 400;
  height: 50px;
}

.tableheading {
  h5 {
    color: #211b3a;
    font-family: Urbanist;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    /* 140% */
  }

  input.form-control {
    max-width: 300px;
    border-radius: 8px;
    padding: 8px 12px;
    overflow: hidden;
    color: #868686;
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.25px;
  }
}
.Subscribed {
  color: #3C9718;
  background: #EDF7E6;
}

.NotSubscribed {
  color: #b72424;
  background: #fee7e7;
}

.table th {
  font-family: Urbanist;
  color: #492fb2;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  /* 114.286% */
  letter-spacing: 0.07px;
}

.table td {
  color: #333;
  font-family: Urbanist;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  /* 21px */
  letter-spacing: 0.25px;
}

.custom-table {
  border: 1px solid #e5e7eb;
  border-radius: 8px;

  thead th {
    background-color: #eee;
    padding: 12px;
  }
}

.badge {
  border-radius: 8px;
  font-family: "Noto Sans";
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: var(--Line-Heights-10, 20px);
  /* 166.667% */
  letter-spacing: var(--Letter-Spacing-4, 0.25px);
  padding: 6px 12px;
}

.Approved {
  color: #3C9718;
  background: #EDF7E6;
}

.Unapproved {
  color: #B77224;
  background: #FEF1E7;
}

.dropdown-menu {
  border-radius: 8px;
  border: 1px solid #c6c6c6;
  background: #fff;
  cursor: pointer;
  padding: 8px 0px;

  .dropdown-item {
    padding: 8px 16px;
    display: flex;
    align-items: center;
    gap: 8px;
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
  }

  .blue {
    color: #3C9718;
  }

  .black {
    color: #211b3a;
  }

  .red {
    color: #b7131a;
  }
}

.btn-check:checked+.btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check)+.btn:active {
  border-color: transparent;
}

.count-text {
  h1 {
    color: var(--Text-Dark, #212121);
    font-family: Urbanist;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    /* 116.667% */
    margin: 0px;
    width: 50px;
  }
}

.percentage {
  padding: 2px 8px;
  font-family: "Noto Sans";
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  /* 133.333% */
  letter-spacing: 0.5px;
  display: flex;
  align-items: center;
}

.success {
  border-radius: 4px;
  background: var(--Success-50, #EDF7E6);
  color: var(--Success-Default---Success, #3C9718);

}

.danger {
  border-radius: 4px;
  color: var(--Danger-Default---Danger, #B7131A);

  background: var(--Danger-50, #FFEEEA);
}



/* Modern pagination style */
.modern-pagination .page-item {
  margin: 0 3px;
}

.modern-pagination .page-link {
  border-radius: 8px !important;
  border: 1px solid #ddd;
  padding: 6px 12px;
  color: #444;
  background-color: #fff;
  transition: all 0.2s ease;
  cursor: pointer;
}

.modern-pagination .page-link.active {
  background-color: #f5f0ff;
  /* light purple */
  border: 1px solid #7c3aed;
  /* purple border */
  color: #7c3aed;
  /* purple text */
  font-weight: 600;
}

.modern-pagination .page-link:hover {
  background-color: #f2f2f2;
  color: #000;
}

.modern-pagination .page-item.disabled .page-link {
  opacity: 0.5;
  pointer-events: none;
}

.form-select {
  display: block;
  width: 100%;
  padding: 0.375rem 2.25rem 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: var(--bs-body-color);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: var(--bs-body-bg);
  background-image: var(--bs-form-select-bg-img), var(--bs-form-select-bg-icon, none);
  background-repeat: no-repeat;
  background-position: right 0.75rem center;
  background-size: 16px 12px;
  border: var(--bs-border-width) solid var(--bs-border-color);
  border-radius: var(--bs-border-radius);
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  cursor: pointer;
}